let offset = 0;
let total = 10;
let ctime = parseInt(Date.now());
export default {
	data() {
		return {
			value: '',
			show: false,
			keywordList: [],
			historyList: [],
			articleList: [],
			loadingText: {
				contentdown: '',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			loadingType: 0
		};
	},
	onLoad() {
		this.historyList = uni.getStorageSync('history_keywords').split(';');
		this.historyList = this.historyList.filter(function(s) {
			return s && s.trim();
		});
		this.init();
	},
	onUnload() {
		offset = 0;
	},
	watch: {
		value(val) {
			this.show = val ? true : false;
			if (!val) {
				offset = 0;
			}
		}
	},
	methods: {
		search(val) {
			console.log(this.value);
			if (!val) {
				return;
			}
			this.value = val;
			if (this.historyList.indexOf(val) == -1) {
				uni.setStorageSync('history_keywords', uni.getStorageSync('history_keywords') ? uni.getStorageSync('history_keywords') + ';' + this.value : this.value);
				this.historyList = uni.getStorageSync('history_keywords').split(';');
				this.historyList = this.historyList.filter(function(s) {
					return s && s.trim();
				});
			}
			uni.showLoading({
				title: '加载中'
			});
			this.api.home.search.get_response_list(
				{
					keyword: this.value,
					ctime,
					offset,
					total
				},
				res => {
					console.log(res);
					this.articleList = res.data;
					uni.hideLoading();
				}
			);
		},
		init() {
			this.api.home.search.get_query_list(
				{
					type: 2,
					ctime,
					offset,
					total
				},
				res => {
					console.log(res);
					this.keywordList = res.data;
				}
			);
		},
		loadMore() {
			console.log('11111');
			if (this.loadingType !== 0) {
				return;
			}
			this.loadingType = 1;
			offset = total + offset;
			this.api.home.search.get_response_list(
				{
					keyword: this.value,
					ctime,
					offset,
					total
				},
				res => {
					console.log(res);
					if (res.data.length) {
						this.articleList = this.articleList.concat(res.data);
						this.loadingType = 0;
					} else {
						this.loadingType = 2;
					}
				}
			);
		},
		clearHistoryList() {
			uni.removeStorageSync('history_keywords');
			this.historyList = [];
		},
		showOperate() {}
	}
};
